.profile {
    width: 300px;
    margin: 0 auto;
    padding-top: 20px;
}


.description {
    padding: 10px 0;
    background-color: #fff;
    border-top-right-radius: 5px;
    border-top-left-radius: 5px;
}

.text {
    margin-bottom: 10px;
    text-align: center;
}

.avatar {
    margin: 0 auto 20px;
    border-radius: 50%;
}

.username {
    composes: text;
    font-size: 19px;
    font-weight: 700;
} 


.tag {
    composes: text;
    color: grey;
}

.location {
    composes: text;
    color: grey;
}


.label {
    margin-bottom: 5px;
    color: grey;
}


.quantity {
    font-size: 17px;
    font-weight: 700;
}

.statList{
    display: flex;
    justify-content: space-between;
    overflow: hidden;
    border-bottom-right-radius: 5px;
    border-bottom-left-radius: 5px;
}


.statListItem{
    padding: 10px 0;
    background-color: whitesmoke;
    width: calc(100% / 3);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}